.screen-left {
  height: 100vh;
  position: relative;
  transition: all 0.2s linear;
}
.screen-left .screen-left-top {
  text-align: center;
  height: 50px;
  background: #222736;
  color: #fff;
  font-size: 16px;
  line-height: 50px;
}
.screen-left .screen-left-content {
  background: #f7f7f7;
  padding-top: 20px;
}
.screen-left .screen-left-content .ant-btn {
  font-size: 14px;
  margin: 10px 5px 0;
  color: #3A89FD;
  border: 1px solid #3A89FD;
}
.screen-left .screen-left-content > div {
  width: 92%;
  background: #ffffff;
  border-radius: 5px;
  box-sizing: border-box;
  margin: 0 0 10px 70px;
  padding: 20px 20px 15px 20px !important;
}
.screen-left .screen-left-content > div h2 {
  font-size: 15px;
  color: #333333;
  font-weight: bold;
  margin: 0;
  margin-left: 10px;
}
.screen-left .screen-left-content > div .ant-row {
  padding: 10px 0;
  transition: all 0.2s linear;
  margin: 5px 0;
  border-bottom: 1px solid #f1f1f1;
  cursor: pointer;
}
.screen-left .screen-left-content > div .ant-row:hover {
  background: rgba(58, 137, 253, 0.05);
  border-radius: 5px;
}
.screen-left .screen-left-content > div .icon-play {
  width: 20px;
  height: 20px;
}
.screen-left .screen-left-content > div .ant-col {
  font-size: 14px;
}
.screen-left .screen-left-content > div .ant-col h3 {
  color: #333333;
  margin-bottom: 0px;
}
.screen-left .screen-left-content > div .ant-col p {
  color: #888888;
  margin: 0;
  font-size: 12px;
}
.screen-left .screen-left-content > div .btn-wrap {
  display: flex;
  justify-content: flex-end;
  padding-top: 14px;
  float: right;
}
.screen-left .screen-left-content > div .btn-wrap span {
  display: block;
  color: #3A89FD;
  line-height: 8px;
  cursor: pointer;
  padding: 5px 15px;
  border-right: 2px solid #E0E0E0;
}
.screen-left .screen-left-content > div .btn-wrap span:last-child {
  border-right: none;
}
.screen-left .screen-left-content > div .btn-wrap span:hover::after {
  display: block;
  width: 100%;
  height: 1px;
  background: #3A89FD;
  position: relative;
  top: 10px;
  content: "";
}
.screen-left .ant-btn:focus {
  background: none;
}
.screen-left .ant-dropdown-menu {
  text-align: center;
}
.screen-left .class-dropdown {
  width: 120px;
  line-height: 50px;
  height: 48px;
  background: none;
  border: none;
  color: #fff;
}
.screen-left .class-dropdown:hover {
  background: none;
  border: none;
  background: #302e45;
  color: #3a89fd;
}
.screen-left .screen-left-content > div h2 {
  font-size: 16px;
  margin-bottom: 10px;
}
.screen-left .screen-left-content > div .ant-col h3 {
  margin-bottom: 5px;
}
.screen-left .screen-left-content > div .btn-wrap span {
  border-right: 1px solid #E0E0E0;
}
.screen-left .screen-left-content > div .ant-row:hover {
  background: rgba(58, 137, 253, 0.05);
  border-radius: 5px;
}
.screen-left .screen-left-content > div .ant-row:last-child {
  border-bottom: none;
}
.screen-left .screen-left-content > div .ant-col p {
  font-size: 14px;
}
.screen-left .screen-left-content > div .ant-row {
  margin: 0;
}
.screen-left-content {
  height: 85%;
}
.screen-left-content .ant-empty {
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.screen-left-content .ant-empty .ant-empty-image {
  width: 324px;
  height: 207px;
  margin-bottom: 20px;
}
.screen-left-content .ant-empty .ant-empty-description {
  font-family: PingFangSC;
  font-size: 14px;
  color: #302E45;
}
.icon-box img {
  width: 20px;
  height: 20px;
}
.resource_box > div {
  max-height: 103%;
  overflow: auto;
}
.resource_box > div::-webkit-scrollbar {
  display: none;
}
.resource_box > div .ant-row {
  min-height: 71px;
}
